:root {
  --auc-primary-color: #9698c3;
  --auc-muted-color: #c4c4c4;
  --auc-dark-color: #0f0f0f;
}

.auc-Recommend-list {
  outline-color: var(--auc-primary-color);
  outline-offset: .5rem;
  grid-template-columns: repeat(2, 1fr);
  gap: .5rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

@media (min-width: 999px) {
  .auc-Recommend-list {
    grid-template-columns: repeat(5, 1fr);
  }
}

.auc-Recommend-item {
  width: 256px;
  height: 495px;
}

.RelatedItem {
  max-width: 256px;
  max-height: 495px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.RelatedItem .relative img {
  width: 100%;
  max-height: 320px;
  margin-left: auto;
  margin-right: auto;
}

.uic-HorizontalSlider-list {
  grid-auto-columns: 300px;
}

